@mixin flexCenter($flex, $center, $center2) {
  display: $flex;
  justify-content: $center;
  align-items: $center2;
}

@mixin fontFamily($family: null, $weight: null, $size: null, $lineHeight: null, $color: null) {
  font-family: $family;
  font-weight: $weight;
  font-size: $size;
  line-height: $lineHeight;
  color: $color;
}